<template>
  <div class="container sign in">
    <main class="contents">
      <div class="sign-form">
        <!-- <h1 class="hdt"></h1>  -->
        <ul class="list">
          <li>
            <label class="dt"></label>
            <div class="dd">
              <span class="input"><input type="email" id="email" placeholder="" v-model="userEmail"></span>
            </div>
          </li>
          <li>
            <label class="dt"></label>
            <div class="dd">
              <span class="input"><input type="password" id="password" v-model="userPassword" placeholder=""></span>
            </div>
          </li>
        </ul>
        <div class="btsbox btn-set">
            <button type="button" class="btn" @click="login"><i class="fa-solid fa-right-to-bracket"></i><em></em> </button>
        </div>
        <div class="link">
            <router-link class="bt" to="/signup">  <i class="fa-solid fa-chevron-right"></i></router-link>
        </div>
      </div>
    </main>

  </div>
</template>

<script>
import { getAuth, setPersistence, signInWithEmailAndPassword, browserSessionPersistence} from 'firebase/auth';
import store from '@/store';
import ui from '../ui.js';
export default {
  name: 'LoginItem',
  props: {
    msg: String
  },
  data() {
    return {
      userEmail:null,
      userPassword:null,
      erMsg : { //https://firebase.google.com/docs/auth/admin/errors?hl=ko&authuser=0
        "auth/user-not-found" :	"       ",
        "auth/wrong-password" :	"   ",
        "auth/too-many-requests" : "        ",
        "auth/weak-password" : " 6   ",
        "auth/missing-email" : "  ",
        "auth/invalid-email" : "     ",
        "auth/email-already-in-use" :	"     ID     ",
        "auth/invalid-phone-number" : "      ",
        "auth/internal-error" : " .",
      }
    }
  },
  beforeRouteEnter: (to, from, next) => {
    console.log(store.state.userInfo.stat);
    console.log(to, from.fullPath);
    if(store.state.userInfo.stat == true) {
      next(from.fullPath)
    }else{
      localStorage.setItem("preurl", location.hash);
      return next();
    }
  },
  created(){
    ui.init();
    
  },
  mounted(){
    document.querySelector(".header .htit").textContent = '';
  },
  methods: {
    async login(){
      const email = this.userEmail;
      const password = this.userPassword;  
      const auth = getAuth();
      ui.loading.show();
      await signInWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        console.log(user.email);
        const gourl = localStorage.getItem("preurl").replace("#","");
        ui.loading.hide();
        ui.alert(""+user.email+ "<br>  !",{
          ycb: ()=>{
            this.$router.push(gourl);
          }
        });
      })
      .catch((error) => {
        console.log( error.code);
        const emsg = this.erMsg[error.code]
        ui.loading.hide();
        ui.alert(  emsg ,{tit:" "});
        // alert( error.code , error.message);
        // const errorCode = error.code;
        // const errorMessage = error.message;
      });
      
      //    https://firebase.google.com/docs/auth/web/auth-state-persistence?hl=ko&authuser=0
      setPersistence(auth, browserSessionPersistence)
      .then(() => {
        return signInWithEmailAndPassword(auth, email, password);
      })
      .catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode,"====",errorMessage);
      });
      
    }     
    
  }
}
</script>

