<template>
  <div class="container sign up">
    <main class="contents">
      <div class="sign-form">
        <!-- <h1 class="hdt"></h1>  -->
        <ul class="list">
          <li>
            <label class="dt"></label>
            <div class="dd">
              <span class="input"><input v-model="userEmail" type="email" placeholder=") test@naver.com"></span>
            </div>
          </li>
          <li>
            <label class="dt"></label>
            <div class="dd">
              <span class="input"><input v-model="userPwd" type="password" placeholder="6  ) 111111 "></span>
            </div>
          </li>
          <li>
            <label class="dt"></label>
            <div class="dd">
              <span class="input"><input v-model="userNick" type="text" placeholder=""></span>
            </div>
          </li>
          <li>
            <label class="dt"></label>
            <div class="dd">
              <!-- {{this.avatarVal}} -->
              <div class="ut-avata">
                <label  v-for="(icon,index) in $store.state.avatar" :key="index">
                  <input type="radio" name="avatar" :value="index" v-model="avatarVal" :checked=" index == 0 ? true : false"> <span class="txt"><img class="img" :src="icon" alt=""></span>
                </label>
              </div>
            </div>
          </li>
        </ul>
        <div class="btsbox btn-set">
            <!-- <router-link class="btn" to="/signin"></router-link> -->
          <button type="button" class="btn" :disabled="isBtnJoin ? false : true" @click="join"><i class="fa-solid fa-right-to-bracket"></i><em></em></button>
        </div>
        <div class="link">
           ? <router-link class="bt" to="/signin">  <i class="fa-solid fa-chevron-right"></i></router-link>
        </div>
      </div>
    </main>

  </div>
</template>

<script>
import db  from '../firebaseConfig.js';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { doc, setDoc  } from "firebase/firestore";
import ui from '../ui.js';

export default {
  name: 'JoinItem',
  props: {
    msg: String
  },
  data() {
    return {
      avatarVal:0,
      userEmail:"",
      userPwd:"",
      userNick:"",
      isBtnJoin:false,
      erMsg : {
        "auth/user-not-found" :	"       ",
        "auth/wrong-password" :	"   ",
        "auth/too-many-requests" : "        ",
        "auth/weak-password" : " 6   ",
        "auth/invalid-email" : "     ",
        "auth/email-already-in-use" :	"     ID     ",
        "auth/invalid-phone-number" : "      ",
        "auth/internal-error" : " .",
      }
    }
  },
  created(){
    ui.init();
    
  },
  mounted(){
    console.log(this.avatarVal);
    document.querySelector(".header .htit").textContent = '';
    // document.querySelectorAll('input[name="avatar"]')[0].checked = true;
  },
  watch:{
    userEmail(){
      this.valCheck();
    },
    userPwd(){
      this.valCheck();
    },
    userNick(){
      this.valCheck();
    }
  },
  methods: {
    valCheck(){
      if (this.userNick.length > 0 && this.userPwd.length >= 6) {
        this.isBtnJoin = true;
      }else{
        this.isBtnJoin = false;
      }
    },
    async join () {
      ui.loading.show();
      const email = this.userEmail;
      const password = this.userPwd;
      const auth = getAuth();
      createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        // Signed in 
        const user = userCredential.user;
        console.log(user  );
        console.log("uid : "+user.uid );
        console.log(": "+ user.accessToken);
        const gourl = localStorage.getItem("preurl").replace("#","");
        this.addMember(user , gourl)

      })
      .catch((error) => {
        console.log( error.code);
        const emsg = this.erMsg[error.code] 
        ui.alert( emsg );
      });
      
    },
    async addMember(user,gourl){
      await setDoc(doc(db, "member" ,user.uid), {
        id: user.uid,
        email: user.email,
        nick : this.userNick,
        avatar: this.avatarVal,
        liked: [],
        date: new Date(),
      }).then(()=>{
        console.log(" : ");
        ui.alert("." ,{
          ycb:()=>{this.$router.push(gourl);}
        });
      }).catch (e =>{
        console.error("  Error : ", e);
      });
    }
        
  }
}
</script>

